@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
    All predefined global values used in the application
 */
:root{
    /*Colors*/
    --color-main-dark: #46515D;
    --color-main-medium: #6F7F92;
    --color-main-light: #B9C1CA;
    --color-gen-attention:#A79576;
    --color-gen-dark: #7F7F7F;
    --color-gen-medium: #F5F5F5;
    --color-gen-light: #F9F9F9;

    --color-background: var(--color-gen-light);
    --color-background-opaque: rgba(0, 0, 0, 0.7);

    /*Font*/
    --font: 'Roboto', sans-serif;

    /*Layout*/
    --border-width: 2px;
    --border-radius: 5px;
    --basic-height: 30pt;
    --basic-margin: 16pt;
}

*{
    /*Global settings*/
    font-family: 'Roboto', sans-serif;
    font-family: var(--font);
    padding: 0;
    margin: 0;
}

html, #root{
    background-color: #F9F9F9;
    background-color: var(--color-background);
    overflow: hidden;
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
}

.App{
    width: 100%;
    height: 100%;
}

/*
    Every page content is wrapped in a page container.
    Set as flexbox to make markup easier.
 */
#page-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* horizontal */
    justify-content: flex-start; /* vertical */
    height: 100%;
    overflow: auto;
    flex: 1 1;

    width: 100%;

    align-self: center;
}
#page-container > div{
    width: 100%;
}

/*
    Horizontal and vertical flexbox classes to easily create flex-boxes
 */
.container-horizontal{
    display: flex;
    flex-direction: row;
    justify-content: flex-start; /* horizontal */
    align-items: flex-start; /* vertical */
    overflow: visible;
}

.container-horizontal > div{
    height: 100%;
    width: 100%;
}

.container-vertical{
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* horizontal */
    justify-content: flex-start; /* vertical */
    overflow: visible;
}

.container-vertical > div{
    width: 100%;
}

/*
    Main container in app to differentiate between the app and loading screen.
 */
#main-container{
    height: 100%;
    width: 100%;
}

/*
    Predefined classes that can be used to markup text
    contains:
       - header
       - title
       - body
       - body-sec
       - body-thr
 */
.text-header{
    width: 100%;
    font-weight: 700;
    color: #46515D;
    color: var(--color-main-dark);
    text-align: left;
    text-decoration: none;
}

.text-title{
    width: 100%;
    font-weight: 700;
    color: #B9C1CA;
    color: var(--color-main-light);
    white-space: nowrap;
    text-align: left;
    text-decoration: none;
    text-transform: uppercase;
}

.text-body{
    width: 100%;
    font-weight: 300;
    color: #7F7F7F;
    color: var(--color-gen-dark);
    text-align: left;
    text-decoration: none;
    word-wrap: break-word;
    white-space: pre-line;
}

.text-body-sec{
    width: 100%;
    font-weight: 300;
    color: #7F7F7F;
    color: var(--color-gen-dark);
    text-align: left;
    text-decoration: none;
    word-wrap: break-word;
    white-space: pre-line;
}

.text-body-thr{
    width: 100%;
    font-weight: 200;
    color: #7F7F7F;
    color: var(--color-gen-dark);
    text-align: left;
    text-decoration: none;
    word-wrap: break-word;
    white-space: pre-line;
}

@media screen and (max-width: 900px){
    :root{
        --basic-margin: 16pt;
    }

    .text-header{
        font-size: 42pt;
    }

    .text-title{
        font-size: 26pt;
    }

    .text-body{
        font-size: 22pt;
    }

    .text-body-sec{
        font-size: 16pt;
    }

    .text-body-thr{
        font-size: 15pt;
    }
}

@media screen and (min-width: 901px){
    :root{
        --basic-margin: 20pt;
    }

    .text-header{
        font-size: 50pt;
    }

    .text-title{
        font-size: 30pt;
    }

    .text-body{
        font-size: 26pt;
    }

    .text-body-sec{
        font-size: 20pt;
    }

    .text-body-thr{
        font-size: 18pt;
    }
}
#menu-container{
    flex: 1 1;

}

#menu-text-container{
    flex: 1 1;
}
.padding-col{
    flex-shrink: 0;
    flex-grow: 0;
    flex-basis: var(--basic-margin);
}

.padding-row{
    flex-basis: var(--basic-margin);
    flex-shrink: 0;
    flex-grow: 0;
}
.button-main{
    background-color: var(--color-main-medium);
    border: var(--border-width) solid var(--color-main-medium);
    border-radius: var(--border-radius);

    text-transform: none;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    color: var(--color-gen-light);
}

.button-main > div:first-child{
    flex: 1 1;
}

.button-main > img{
    height: 100%;
    width: auto;;
}
button{
    border: none;
    background-color: var(--color-main-light);
    color: var(--color-main-dark);
    padding:  calc(var(--basic-margin) * 0.4) var(--basic-margin);
    text-transform: uppercase;
    border-radius: var(--border-radius);
    font-weight: bold;
}
.button-sec{
    background-color: var(--color-gen-light);
    border: var(--border-width) solid var(--color-main-medium);
    border-radius: var(--border-radius);

    text-transform: none;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    color: var(--color-main-medium);
}

.button-sec > div:first-child{
    flex: 1 1;
}

.button-sec > img{
    height: 100%;
    width: auto;;
}
.header-container{
    width: 100%;
    align-items: center;
    background-color: var(--color-main-dark);
}

.header-title{
    color: var(--color-gen-attention);
}

.header-content{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.header-logo{
    flex-grow: 0;
    height: 45px;
}
.button-icon{
    background-color: transparent;
    border: 1px solid var(--color-main-light);
    border-radius: var(--border-radius);
    text-transform: none;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    padding:  calc(var(--basic-margin) * 0.3) calc(var(--basic-margin) * 1.1);
}

.button-icon > img{
    height: 16pt;
    width: auto;
}
#explore-container{
    flex: 1 1;

}

#explore-text-container{
    flex: 1 1;
}

.button-padding{
    height: 10px;
}

.selector-container{
    height: auto;
}


.selector-content-container{
    justify-content: space-around;
    align-items: stretch;
    min-height: 150px;
}

.selector-content-container> button{
    padding: 0;
    background-color: var(--color-main-dark);
    border: none;

    flex-grow: 0;
    flex-basis: 70px;
    flex-shrink: 0;
}

.selector-content-container > button:first-child{
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.selector-content-container >button:last-child{
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.selector-content{
    /*border-top: var(--border-width) solid var(--color-main-dark);*/
    /*border-bottom: var(--border-width) solid var(--color-main-dark);*/
    justify-content: center;
    align-items: center;
    align-self: center;
    flex: 1 1;
    white-space: normal;
    text-wrap: normal;
}


.book-overview-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
}

.book-overview-container > div{
    border-radius: var(--border-radius);
    width: 100%;
    flex: 1 1;
    min-width: 250px;
    max-width: 450px;
}

.book-overview-container > div > img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.loading-container{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid var(--color-main-dark);
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: var(--color-main-dark) transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
}
@keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

